.form-section__subtitle {
  width: fit-content;
  position: relative;
  .set-font(24px, 26px);
  font-weight: normal;
  margin: 27px 0 32px;

  &:first-of-type {
    margin-top: 6px;
  }

  &::after {
    content: "";
    position: absolute;
    top: 15px;
    left: calc(100% + 22px);
    width: 100vw;
    height: 3px;
    background-color: @choose-program-color;
    display: block;
  }
}

.form-section__subtitle__master-title {
  max-width: 187px;
}

.form-section__comment-text {
  .set-font(16px, 18px);
  text-transform: uppercase;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid @input-border-color;
  padding: 10px 15px 0 12px;
  margin-top: 2px;
}

@media (min-width: @tablet-size) {
  .form-section__subtitle {
    .set-font-equal(36px);
    margin-bottom: 76px;
    margin-top: 65px;
    max-width: unset;

    &::before {
      content: "";
      position: absolute;
      display: block;
      height: 3px;
      background-color: @choose-program-color;
      right: calc(100% + 40px);
      top: 20px;
      margin-bottom: -2px;
      width: 100vh;
    }

    &::after {
      top: 20px;
    }
  }
}
