@font-face {
  font-family: "Oswald";
  src: url("../fonts/Oswald-Regular.woff2") format("woff2"), url("../fonts/Oswald-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OswaldExtraLight";
  src: url("../fonts/Oswald-ExtraLight.woff2") format("woff2"), url("../fonts/Oswald-ExtraLight.woff") format("woff");
  font-weight: 200;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OswaldLight";
  src: url("../fonts/Oswald-Light.woff2") format("woff2"), url("../fonts/Oswald-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OswaldSemiBold";
  src: url("../fonts/Oswald-SemiBold.woff2") format("woff2"), url("../fonts/Oswald-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OswaldMedium";
  src: url("../fonts/Oswald-Medium.woff2") format("woff2"), url("../fonts/Oswald-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OswaldBold";
  src: url("../fonts/Oswald-Bold.woff2") format("woff2"), url("../fonts/Oswald-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  display: none;
}
.main-page {
  display: grid;
  grid-template-columns: 20px 1fr 20px;
  min-width: 320px;
  margin: 0;
  padding: 0;
}
.page-element {
  grid-column-start: 2;
  grid-column-end: 3;
  box-sizing: border-box;
  overflow-x: hidden;
}
@media (min-width: 768px) {
  .main-page {
    grid-template-columns: 30px 1fr 30px;
  }
}
@media (min-width: 1440px) {
  .main-page {
    display: grid;
    grid-template-columns: 110px 1fr 110px;
  }
  .page-element {
    grid-column-start: 2;
    grid-column-end: 3;
  }
}
.page-header {
  grid-column-start: 1;
  grid-column-end: -1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  border-bottom: 1px solid #D9D9D9;
}
@media (min-width: 768px) {
  .page-header {
    grid-column-start: 2;
    grid-column-end: 3;
    border: none;
    margin-top: 24px;
  }
}
@media (min-width: 1440px) {
  .page-header {
    grid-column-start: 1;
    grid-column-end: -1;
    padding: 54px 110px 0;
    margin-top: 0;
  }
}
@media (min-width: 768px) {
  .current-page {
    position: relative;
  }
  .current-page::after {
    content: "";
    display: block;
    position: absolute;
    height: 2px;
    width: 100%;
    background-color: #68B738;
    bottom: -8px;
  }
}
@media (min-width: 1440px) {
  .current-page:nth-child(2)::after {
    background-color: #FFF;
  }
}
.menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style-type: none;
  font-family: Oswald, "Arial", sans-serif;
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  padding: 0;
  flex-grow: 1;
  margin: 0;
}
.menu__link {
  color: #000;
  text-decoration: none;
}
.menu__item {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  height: 66px;
  box-sizing: border-box;
}
.menu__item:first-child {
  padding-left: 20px;
  padding-right: 23px;
  justify-content: space-between;
}
.menu__item:not(:last-child) {
  border-bottom: 1px solid #E6E6E6;
}
@media (min-width: 768px) {
  .menu__item {
    height: fit-content;
    width: fit-content;
    margin-left: 34px;
    margin-right: 2px;
  }
  .menu__item:first-child {
    margin: 0;
    margin-right: auto;
    padding: 0;
  }
  .menu__item:not(:last-child) {
    border: none;
  }
  .menu__item:hover {
    opacity: 0.6;
  }
  .menu__item:active {
    opacity: 0.3;
  }
  .menu {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }
}
@media (min-width: 1440px) {
  .menu__link__white {
    color: #FFF;
  }
  .menu__item:first-child {
    margin-left: 0;
  }
}
.title {
  flex-grow: 2;
}
.title-images {
  display: flex;
}
.title__logo {
  display: block;
  height: 38px;
}
.title__text {
  height: 18px;
  margin: auto auto;
}
@media (min-width: 768px) {
  .title__text {
    display: none;
  }
  .title__logo {
    height: 54px;
  }
  .title {
    display: block;
  }
  .title:hover {
    opacity: 0.8;
  }
  .title:active {
    opacity: 0.6;
  }
}
.burger-layer {
  content: "";
  position: absolute;
  background-color: #000;
  display: block;
  width: 24px;
  height: 2px;
}
.burger-menu {
  width: 24px;
  height: 16px;
}
.burger-menu__layer {
  position: relative;
}
.burger-menu__layer.first::before {
  content: "";
  position: absolute;
  background-color: #000;
  display: block;
  width: 24px;
  height: 2px;
  top: 0;
}
.burger-menu__layer.second::before {
  content: "";
  position: absolute;
  background-color: #000;
  display: block;
  width: 24px;
  height: 2px;
  top: 8px;
}
.burger-menu__layer.third::before {
  content: "";
  position: absolute;
  background-color: #000;
  display: block;
  width: 24px;
  height: 2px;
  top: 16px;
}
@media (min-width: 768px) {
  .burger-menu {
    display: none;
  }
}
.main-content {
  display: grid;
  grid-template-columns: 20px 1fr 20px;
  align-self: center;
  grid-column-start: 1;
  grid-column-end: -1;
}
.main-content__element {
